import React from 'react';

import findIndex from 'lodash/findIndex';
import { withTranslation } from 'react-i18next';
import { createMuiTheme, withStyles, MuiThemeProvider } from '@material-ui/core/styles';

import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import IconButton from '@material-ui/core/IconButton';

// import BackgroundImage from '../../images/bg1.jpg';

const theme = outerTheme =>
  createMuiTheme({
    ...outerTheme,
    palette: Object.assign(
      {},
      {
        primary: outerTheme.palette.primary,
        secondary: outerTheme.palette.secondary,
        error: outerTheme.palette.error,
        type: 'dark'
      }
    )
  });

const styles = theme => ({
  selectedTab: {
    color: 'white',
    background: 'rgba(255,255,255,0.2)'
  },
  backgrounded: {
    backgroundImage: 'url(images/bg1.jpg)',
    backgroundPosition: 'center',
    backgroundSize: 'cover'
  },
  overlay: {
    background: 'rgba(0,0,0,0.5)'
  },
  whiteText: {
    color: 'white'
  },
  tabs: {
    width: '100%'
  }
});

const Header = props => {
  const {
    classes,
    t,
    i18n,
    match: {
      params: { page = '' }
    }
  } = props;

  const handleChange = (event, value) => {
    props.history.push(pages[value].link);
  };

  const pages = [
    { link: '/', title: t('Personal data') },
    { link: '/educations', title: t('Educations') },
    { link: '/experiences', title: t('Experiences') },
    { link: '/skills', title: t('Skills') },
    { link: '/contact', title: t('Contact') }
  ];

  const value = findIndex(pages, x => x.link === `/${page}`);

  return (
    <MuiThemeProvider theme={theme}>
      <AppBar className={classes.backgrounded}>
        <div className={classes.overlay}>
          <Toolbar>
            <div style={{ display: 'flex', flex: 1, flexDirection: 'column', padding: '15px' }}>
              <div style={{ display: 'flex', alignItems: 'center', flex: 1 }}>
                <Typography component="h1" variant="h5" className={classes.whiteText}>
                  {t('pdata:Balazs Gallay')}
                </Typography>
              </div>
              <Typography component="h3" variant="subtitle1" className={classes.whiteText}>
                {t('Software developer')}
              </Typography>
            </div>

            <div
              style={{
                display: 'flex',
                flex: 1,
                flexDirection: 'row',
                justifyContent: 'flex-end',
                alignItems: 'center'
              }}>
              <IconButton onClick={() => window.open('https://facebook.com/gallayb')}>
                <i className="fa fa-facebook-square" />
              </IconButton>
              <IconButton
                onClick={() => window.open('https://linkedin.com/in/balázs-gallay-b9b1b834')}>
                <i className="fa fa-linkedin-square" />
              </IconButton>
              <IconButton onClick={() => window.open('https://plus.google.com/u/0/+BalázsGallay')}>
                <i className="fa fa-google-plus-square" />
              </IconButton>
              <IconButton onClick={() => window.open('https://github.com/carathorys')}>
                <i className="fa fa-github-square" />
              </IconButton>
              <Button
                dense="true"
                raised={i18n.language === 'hu-HU' ? 'true' : 'false'}
                color={i18n.language === 'hu-HU' ? 'primary' : 'default'}
                onClick={() => i18n.changeLanguage('hu-HU')}>
                {t('Hungarian')}
              </Button>
              <Button
                dense="true"
                raised={i18n.language === 'en' ? 'true' : 'false'}
                color={i18n.language === 'en' ? 'primary' : 'default'}
                onClick={() => i18n.changeLanguage('en')}>
                {t('English')}
              </Button>
            </div>
          </Toolbar>

          <Toolbar style={{ minHeight: 48 }}>
            <Tabs
              value={value}
              onChange={handleChange}
              indicatorColor="primary"
              variant="scrollable"
              // scrollable
              // scrollButtons="on"
              className={classes.tabs}
            >
              {pages.map((page, index) => (
                <Tab
                  key={page.link}
                  label={page.title}
                  className={value === index ? classes.selectedTab : ''}
                />
              ))}
            </Tabs>
          </Toolbar>
        </div>
      </AppBar>
    </MuiThemeProvider>
  );
};

export default withTranslation('header')(withStyles(styles)(Header));
